/**
 *   @author:  kenzyyang
 *   @date:  2019-4-22
 *   @desc:  
 *   @method:  POST
 *   @param:  password  string  
 * */
const changeUserPassword = (data) => {
    const url = '/user/userChangePassword';
    const {password, id, role} = data;
    const params = {
        id,
        role,
        password: Sha1(password)
    };
    return axios.post(url, params);
};

